.gnb-nav {
  @include flex-align-center(v);
  position: fixed;
  z-index: 1000;
  padding-right: 35px;
  width: 100%;
  height: 125px;
  background-color: $black;
  transition: all 0.3s ease;
  div {
    @include flex-align-center(h);
    margin-left: 45px;
    text-transform: capitalize;

    a {
      text-decoration: none;
      color: $basic-color; } }
  .gnb-home {
    margin: 35px; }
  .gnb-nav-list {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .active a {
      border-bottom: 2px solid $basic-color; }
    .active a, div:hover a {
      color: #fff; }
    [class*="gnb-page"] {
      line-height: 2.5;
      font-family: $mont;
      font-size: 11px;
      font-weight: 400;
      letter-spacing: 0.2em;
      text-transform: uppercase;
      text-align: {} } }

  &.scrolled {
    height: 102px;
    background-color: #fff;
    .active a, div:hover a {
      color: $black; } } }


.lg {
  .gnb-nav-list {
    justify-content: space-around; } }


